			}
	
	.add-update-preview .btn3 {
				border: 0px solid #ccc;
				cursor: pointer;
				border-radius: 5px;
				padding: 0 10px;
				margin: 0 10px 0 0;
				color: #fff;
				background: #49AACE;
				width: 120px;
